<template>
  <div class="storage-add-malt">
    <form @submit.prevent="addMalt">
          <label for="name">Name</label>
          <input type="text" name="name" id="name" v-model="malt.name" placeholder="Name">
          <label for="price">Preis</label>
          <input type="text" name="price" id="price" v-model="malt.price" placeholder="Preis">
          <label for="ebcMin">EBC (min)</label>
          <input type="text" name="ebcMin" id="ebcMin" v-model="malt.ebcMin" placeholder="EBC (min)">
          <label for="ebcMax">EBC (max)</label>
          <input type="text" name="ebcMax" id="ebcMax" v-model="malt.ebcMax" placeholder="EBC (max)">
          <label for="amount">Menge</label>
          <input type="text" name="amount" id="amount" v-model="malt.amount" placeholder="Menge">
          <label for="ground">Geschrotet</label>
          <input type="text" name="ground" id="ground" v-model="malt.ground" placeholder="Geschrotet">
          <label for="shop">Laden</label>
          <input type="text" name="shop" id="shop" v-model="malt.shop" placeholder="Laden">
          <label for="dateOfPurchase">Kaufdatum</label>
          <input type="date" name="dateOfPurchase" id="dateOfPurchase" v-model="malt.dateOfPurchase" placeholder="Kaufdatum">
          <label for="useByDate">Verfallsdatum</label>
          <input type="date" name="useByDate" id="useByDate" v-model="malt.useByDate" placeholder="Verfallsdatum">
          <button Type="submit">Malz hinzufügen</button>
      </form>
  </div>
</template>

<script>
import api from "@/js/backend-api";
import storageMemory from "@/js/StorageMemory"

export default {
  name: 'StorageAddMalt',

  data () {
      return {
        malt: {
          name: '',
          price: '',
          ebcMin: '',
          ebcMax: '',
          amount: '',
          ground: '',
          shop: '',
          dateOfPurchase: '',
          useByDate: ''
        },
        response: null
      }
    },
    methods: {
      addMalt() {
        api.addMalt(this.malt).then( response => {
            console.log('Created new Malt with id ' + response.data);
            storageMemory.setMaltNeedsUpdate(true);
        });
      }
    }
}

</script>

