<template>
  <div class="storage">
    <storageOverviewMalt v-bind:allMalts="allMalts"></storageOverviewMalt>
    <router-link to="/MalzHinzufügen">Malz hinzufügen</router-link>
    <storageOverviewHops v-bind:allHops="allHops"></storageOverviewHops>
    <router-link to="HopfenHinzufügen">Hopfen hinzufügen</router-link>
  </div>
</template>

<script>
import backendApi from "@/js/backend-api"
import storageMemory from "@/js/StorageMemory"
import storageOverviewMalt from "./StorageOverviewMalt"
import storageOverviewHops from "./StorageOverviewHops"

export default {
  name: 'storage',
  components: {
    storageOverviewMalt,
    storageOverviewHops
  },
  data () {
      return {
        allMalts: null,
        allHops: null,
        errors: []
      }
    },
  methods: {
    callMaltApi () {
      backendApi.malt().then(response => {
        console.log("in callMaltApi " + response.data);
          this.allMalts = response.data;
          storageMemory.setAllMalts(this.allMalts);
          storageMemory.setMaltNeedsUpdate(false);
      })
      .catch(error => {
        this.errors.push(error);
      })
    },
    callHopsApi () {
      backendApi.hops().then(response => {
        console.log("in callHopsApi " + response.data);
          this.allHops = response.data;
          storageMemory.setAllHops(this.allHops);
          storageMemory.setHopsNeedsUpdate(false);
      })
      .catch(error => {
        this.errors.push(error);
      })
    },
  },
  beforeMount(){
    if (storageMemory.getMaltNeedsUpdate()) {
      this.callMaltApi();
    } else {
      this.allMalts = storageMemory.getAllMalts();
    }
    if (storageMemory.getHopsNeedsUpdate()) {
      this.callHopsApi();
    } else {
      this.allHops = storageMemory.getAllHops();
    }
  },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1 {
  font-weight: normal;
}
</style>
