import axios from 'axios'

const AXIOS = axios.create({
  baseURL: `/api`,
  timeout: 1000
});


export default {
    // example app
    hello() {
        return AXIOS.get(`/hello`);
    },
    getUser(userId) {
        return AXIOS.get(`/user/` + userId);
    },
    createUser(firstName, lastName) {
        return AXIOS.post(`/user/` + firstName + '/' + lastName);
    },
    getSecured(user, password) {
        return AXIOS.get(`/secured/`,{
            auth: {
                username: user,
                password: password
            }});
    },

    // bms
    malt() {
        return AXIOS.get('/malts');
    },
    hops() {
        return AXIOS.get('/hops');
    },
    addMalt(malt) {
        return AXIOS.post(`/storage/addMalt/` + malt.name);
    },
    addHops(hops) {
        let url = `/storage/addHops/${hops.name}/${hops.alpha}/${hops.price}/${hops.amount}/${hops.shop}/${hops.dateOfPurchase}/${hops.useByDate}`;
        return AXIOS.post(url);
    }
}


