export default {
    maltNeedsUpdate: true,
    hopsNeedsUpdate: true,
    allMalts: [],
    allHops: [],
    setMaltNeedsUpdate (booleanValue) {
        this.maltNeedsUpdate = booleanValue;
    },
    getMaltNeedsUpdate () {
        return this.maltNeedsUpdate;
    },
    setHopsNeedsUpdate (booleanValue) {
        this.hopsNeedsUpdate = booleanValue;
    },
    getHopsNeedsUpdate () {
        return this.hopsNeedsUpdate;
    },
    getAllMalts() {
        return this.malts;
    },
    setAllMalts(malts) {
        this.malts = malts;
    },
    getAllHops() {
        return this.hops;
    },
    setAllHops(hops) {
        this.hops = hops;
    }
}