<template>
  <div class="storage-add-hops">
    <form @submit.prevent="addHops">
          <label for="name">Name</label>
          <input type="text" name="name" id="name" v-model="hops.name" placeholder="Name">
          <label for="alpha">Alpha</label>
          <input type="text" name="alpha" id="alpha" v-model="hops.alpha" placeholder="Alpha">
          <label for="price">Preis</label>
          <input type="text" name="price" id="price" v-model="hops.price" placeholder="Preis">
          <label for="amount">Menge</label>
          <input type="text" name="amount" id="amount" v-model="hops.amount" placeholder="Menge">
          <label for="shop">Laden</label>
          <input type="text" name="shop" id="shop" v-model="hops.shop" placeholder="Laden">
          <label for="dateOfPurchase">Kaufdatum</label>
          <input type="date" name="dateOfPurchase" id="dateOfPurchase" v-model="hops.dateOfPurchase" placeholder="Kaufdatum">
          <label for="useByDate">Verfallsdatum</label>
          <input type="date" name="useByDate" id="useByDate" v-model="hops.useByDate" placeholder="Verfallsdatum">
          <button Type="submit">Hopfen hinzufügen</button>
      </form>
  </div>
</template>

<script>
import backendApi from "@/js/backend-api";
import storageMemory from "@/js/StorageMemory"

export default {
  name: 'StorageAddHops',

  data () {
      return {
        hops: {
          name: '',
          alpha: '',
          price: '',
          amount: '',
          shop: '',
          dateOfPurchase: '',
          useByDate: ''
        },
        response: null
      }
    },
    methods: {
      addHops() {
        backendApi.addHops(this.hops).then( response => {
            console.log('Created new Hops with id ' + response.data);
            storageMemory.setHopsNeedsUpdate(true);
        });
      }
    }
}

</script>

